<template>
	<div class="pbl">
		<div class="pb">
			<div class="item" id="pb1"></div>
			<div class="item" id="pb2"></div>
		</div>	
		<div class="ts">点击继续加载</div>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				DATAimg:[
					{imgUrl:''},
					{imgUrl:require('../../assets/images/pb/1.jpg')},
					{imgUrl:require('../../assets/images/pb/2.jpg')},
					{imgUrl:require('../../assets/images/pb/3.jpg')},
					{imgUrl:require('../../assets/images/pb/4.jpg')},
					{imgUrl:require('../../assets/images/pb/5.jpg')},
					{imgUrl:require('../../assets/images/pb/6.jpg')},
					{imgUrl:require('../../assets/images/pb/7.jpg')},
					{imgUrl:require('../../assets/images/pb/8.jpg')},
					{imgUrl:require('../../assets/images/pb/9.jpg')},
					{imgUrl:require('../../assets/images/pb/10.jpg')},
				]
			}
		},
		mounted(){

			let imgId = 0;
			let that = this;
			let ts = document.querySelector('.ts');
			let b =[];

			this.DATAimg.forEach(item =>{
				b.push(item.imgUrl)
			})

			function mindiv() {
				let pb1 = document.getElementById('pb1');
				let pb2 = document.getElementById('pb2');

				let pbimgs1 = pb1.children;
				let pbimgs2 = pb2.children;

				let pb1height = jisuan(pbimgs1);
				let pb2height = jisuan(pbimgs2);

				let minheight = Math.min(pb1height, pb2height);

				if (minheight == pb1height) {
					return pb1;
				}
				if (minheight == pb2height) {
					return pb2;
				}
			}

			function jisuan(pbimgs) {
				if (pbimgs == null || pbimgs.length == 0) {
					return 0
				}else{
					let height = 0;
					for (let i = 0; i < pbimgs.length; i++){
						height += pbimgs[i].height;
					}
					return height;

				}
			}

			function insertImg() {
				let inter = setInterval( function() {
					imgId++;
					if (imgId > 9) {
						clearInterval(inter);
						ts.style.display = 'block';
					}
					/*if (ts.onclick()) {
						imgId = 0
					}*/
					let img = document.createElement('img');
					img.setAttribute("src",b[imgId]);
					
					mindiv().appendChild(img);
				},100)
			}

			insertImg();

			ts.onclick = function(){
				imgId = 0;
				insertImg();
			}


		}
	}
</script>

<style lang="scss">
	.pb{
		display: flex;
		flex-direction: row;
		.item{
			flex:1;
			margin:0 .1rem;
			img{
				width: 100%;
				height: auto;
				margin: .1rem 0;
			}
		}
	}
	.ts{
		width: 100%;
		height: .8rem;
		line-height: .8rem;
		text-align: center;
		background: green;
		position: absolute;
		left: 0;
		color:white;
		display: none;
	}
</style>